// -------------------------
// SLICK SLIDER
// -------------------------

/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    .ref-block {
        position: absolute;
        bottom: 0;
        background-color: rgba(26, 65, 122, .7);
        color: white;
        left: 0;
        right: 0;
        padding: 10px 20px;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}


@charset "UTF-8";

// Default Variables

// Slick icon entity codes outputs the following
// "\2190" outputs ascii character ""
// "\2192" outputs ascii character ""
// "\2022" outputs ascii character ""

$slick-font-path: "./fonts/" !default;
$slick-font-family: "slick" !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: white !default;
$slick-dot-color: $quaternary !default;
$slick-dot-color-active: $primary !default;
$slick-prev-character: "\2190" !default;
$slick-next-character: "\2192" !default;
$slick-dot-character: "\2022" !default;
$slick-dot-size: 16px !default;
$slick-opacity-default: 1 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: .75 !default;

@function slick-image-url($url) {
    @if function-exists(image-url) {
        @return image-url($url);
    }
    @else {
        @return url($slick-loader-path + $url);
    }
}

@function slick-font-url($url) {
    @if function-exists(font-url) {
        @return font-url($url);
    }
    @else {
        @return url($slick-font-path + $url);
    }
}

/* Slider */

.slick-list {
    .slick-loading & {
        background: #fff url("../images/ajax-loader.gif") center center no-repeat;
    }
}

/* Icons */
@if $slick-font-family == "slick" {
    @font-face {
        font-family: "slick";
        src: slick-font-url("slick.eot");
        src: slick-font-url("slick.eot?#iefix") format("embedded-opentype"), slick-font-url("slick.woff") format("woff"), slick-font-url("slick.ttf") format("truetype"), slick-font-url("slick.svg#slick") format("svg");
        font-weight: normal;
        font-style: normal;
    }
}

/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 20px;
    width: 20px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    outline: none;
    &:hover, &:focus {
        outline: none;
        background: transparent;
        color: transparent;
        &:before {
            opacity: $slick-opacity-on-hover;
        }
    }
    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
    &:before {
        font-family: $slick-font-family;
        font-size: 20px;
        line-height: 1;
        color: $slick-arrow-color;
        opacity: $slick-opacity-default;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.slick-prev {
    left: 0px;
    [dir="rtl"] & {
        left: auto;
        right: -25px;
    }
    &:before {
        content: $slick-prev-character;
        [dir="rtl"] & {
            content: $slick-next-character;
        }
    }
}
.slick-next {
    right: 0px;
    [dir="rtl"] & {
        left: -25px;
        right: auto;
    }
    &:before {
        content: $slick-next-character;
        [dir="rtl"] & {
            content: $slick-prev-character;
        }
    }
}
/* Dots */
.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: 0px;
    left: 0;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 20px;
                height: 20px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;

                content: "\f04d";
                font-family: FontAwesome;
                @include transition;
            }
        }
        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }
}
.slick-slide:focus {outline:0;}
.slick-slider--02{
    margin-bottom: 5px;
}
.slick-nav--02{
    .slick-arrow:before {color: $primary;}
}
.slick-slider--01{
    .slick-arrow:before {color: $primary;}
}
.slick-slider--04{
  padding: 20% 5%;
  .achievements{
    padding: 0 10px;
    li{
      color: #fff;
      &:before{
        color: #fff;
      }
    }
    .title{
      font-size: 22px;
    }

  }
  .achievements li:before {
      color: #fff;
  }
  .slick-dots {
    li {
      button {
        &:before {
          color: white;
        }
      }
      &.slick-active button:before {
        color: white;
      }
    }
  }
}

@media (min-width: 768px){
    .slick-prev {
        left: -20px;
    }
    .slick-next {
        right: -20px;
    }
}
@media (min-width: 768px){
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-prev.slick-arrow{
    left: 120px;
    z-index: 999;
  }
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-next.slick-arrow{
    right: 120px;
    z-index: 999;
  }
}
@media (min-width: 992px){
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-prev.slick-arrow{
    left: 210px;
  }
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-next.slick-arrow{
    right: 210px;
  }
}
@media (min-width: 1200px){
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-prev.slick-arrow{
    left: 280px;
  }
  #cds-graphic--wrapper > div.graphic--wrapper > div > div > div > button.slick-next.slick-arrow{
    right: 280px;
  }
}


#img-actions > div.slick-slider--02.slick-initialized.slick-slider > div,
#events > div.slick-slider--02.slick-initialized.slick-slider > div {
    height: 133px !important;
}
@media (min-width: 768px){
    #img-actions > div.slick-slider--02.slick-initialized.slick-slider > div,
    #events > div.slick-slider--02.slick-initialized.slick-slider > div {
        height: 374px !important;
    }
}

.filter-content--01{
  .slick-prev:before, .slick-next:before {
    color: #42567c;
  }
}