    0% {-webkit-transform: scale(0);opacity: 0.0;}
    60% {-webkit-transform: scale(1.1);}
    80% {-webkit-transform: scale(0.9);opacity: 1;}   
    100% {-webkit-transform: scale(1);opacity: 1;}       
}

